import React, { useCallback } from 'react';
import { DownloadNormal, Tooltip } from '@ali/we-design';
import { PreviewIconWrapper, WrapTooltip } from './styled';

interface Props {
  title?: string;
  onDownloadFile?: any;
  allowDownload?: boolean;
}

function EmbedDownloadButton(props: Props) {
  const { title, onDownloadFile, allowDownload = true } = props;

  const handleDownLoad = useCallback((event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    event.stopPropagation();
    onDownloadFile();
  }, [onDownloadFile]);

  if (!allowDownload) return null;

  return (
    <PreviewIconWrapper onClick={handleDownLoad}>
      <WrapTooltip>
        <Tooltip placement="top" title={title}>
          <DownloadNormal />
        </Tooltip>
      </WrapTooltip>
    </PreviewIconWrapper>
  );
}

export default EmbedDownloadButton;
